.minus-icon:hover {
  filter: invert(0.5) saturate(5)  brightness(5)
}

.vertically-centered {
  height: '32px';
  display: 'flex';
  align-items: 'center';
}

.grid {
  height: 100%;
  display: grid;
  height: calc(100vh - 30px);
  width: calc(100vw - 30px);
  row-gap: 5px;
  column-gap: 5px;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
}

.header {
  grid-area: 1 / 1 / span 1 / span 1;
}

.content-container {
  height: 100%;
  grid-area: 2 / 1 / span 1 / span 1;
  background-color: #696B69;
  padding: 5px;
}

.content {
  height: calc(79vh - 40px);
  overflow-y: scroll;
  background-color: #B3B7B4;
}

.ingredient {
  cursor: pointer;
  user-select: none;
}

.ingredient:hover {
  font-weight: bold;
}

.footer {
  grid-area: 3 / 1 / span 1 / span 1;
}

